* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  text-align: center;
  margin: 10%;
  font-size: 20px;
}

.dragon-avatar-image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.dragon-avatar-image-pattern {
  position: absolute;
}

.dragon-avatar-image-background {
  position: absolute;
}

.dragon-avatar-image {
  z-index: 0;
}

.signout-button {
  position: absolute;
  right: 30px;
  top: 30px;
}

.account-dragon-row-input {
  width: 100px;
  height: 20px;
  margin-right: 15p;
}
